 be generated: "),_c('ul',[_c('li',[_vm._v(" offensive: threat created - threat lost. Offensive threat is very comparable to xG (and even has slightly better correlation with goals scored) ")]),_c('li',[_vm._v(" defensive: threat prevented - threat conceded. Defensive threat allows us to look at how many goals a player prevents/allows out of possession ")]),_c('li',[_vm._v(" total: offensive + defensive. Total threat is an incredibly effective predictor of game outcomes and effectively tells how much a player's actions should impact the scoreline. ")])]),_vm._v(" Each action a player takes is assigned a threat value. Many actions only impact 1 of the 4 basic types of threat. Many others impact 2 (for example, losing possession adds to threat lost and threat conceded to account for the loss of a scoring opportunity and the creation of one for the other team) Determining how an action impacts threat is sometimes straightforward (for example, passing). Often, however, there is some ambiguity. When I made these determinations, I followed a few basic principles: "),_c('ul',[_c('li',[_vm._v(" Every defensive action should have direct offensive equivalents. For example, winning a duel should ALWAYS be accompanied by loss of possession for the offense. Ideally, this works in reverse, but in reality it's impossible to assign defensive threats to actions such as passing and carrying. ")]),_c('li',[_vm._v(" In general, threat created/lost/prevented/conceded is simply threat after - threat before. This means that sometimes a player is penalized or rewarded for the actions of other players or the unintended consequences of their own actions. I am comfortable with this given the size of the dataset; any anomalies should work themselves out in aggregate or be indicative of actual footballing ability. ")]),_c('li',[_vm._v(" One action results in a change in threat by one player. If an action affects multiple players, there should be multiple events in the data that allow each playe